<template>
    <div>
        <!-- 头部 -->
         <div class="topnav">
            <van-nav-bar
                title="收货地址"
                left-arrow
                @click-left="onClickLeft"
            />
        </div>
        <!-- 内容 -->
        <div class="content">
            <van-address-edit
                :area-list="areaList"
                show-search-result
                :search-result="searchResult"
                :area-columns-placeholder="['请选择', '请选择', '请选择']"
                @save="onSave"

            />
        </div>


    </div>
</template>

<script>

import areaList from "./erea.js"

    export default {
         data() {
            return {
                areaList,
                searchResult: [],
            };
        },
         methods: {
            onClickLeft() {
                this.$router.go(-1)
            },
            onSave({name,tel,province,city,county,areaCode,addressDetail}) {
                // 调用vuex方法
                this.$store.commit("address/add",{
                    name,
                    tel,
                    address:province+city+county+addressDetail,
                    province,
                    city,
                    county,
                    areaCode,
                    addressDetail
                });
                this.$router.go(-1); //回到上一页
            },
           
        },
            
        
       
      
    }
</script>

<style lang="scss" scoped>

</style>